import React, { useState } from 'react';
import styled from 'styled-components';

import { Header, Label, BEWrapper } from '../../PrimaryStyles';

import StickerImg1 from "../../../res/Sticker_App_Cen_01.png";
import { connect } from 'react-redux';

import { selectThemeBackgroundColor, selectThemeMainAccentColor, selectThemeFontColor } from '../../../store/themeSlice';

const Triangle = ({ w = '30', h = '30', direction = 'right', color}) => {
    const points = {
      top: [`${w / 2},0`, `0,${h}`, `${w},${h}`],
      right: [`0,0`, `0,${h}`, `${w},${h / 2}`],
      bottom: [`0,0`, `${w},0`, `${w / 2},${h}`],
      left: [`${w},0`, `${w},${h}`, `0,${h / 2}`],
    }
  
    return (
      <svg width={w} height={h}>
        <polygon points={points[direction].join(' ')} fill={color} />
      </svg>
    )
}

const LeftArrow = styled.div`
    grid-column: a / b;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    cursor: pointer;
    transition: transform ease 0.2s;
    :hover {
        transform: translate(-20%) scale(120%);
    }
`
const RightArrow = styled.div`
    grid-column: f / g;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    cursor: pointer;
    transition: transform ease 0.2s;
    :hover {
        transform: translate(20%) scale(120%);
    }
`

const ProjectWrapper = styled(BEWrapper)`
    grid-template-columns: [a] 6% [b] 22% [c] 22% [d] 22% [e] 22% [f] 6% [g];
    grid-column: b / e;
    grid-row: 1 / -1;
    @media (max-width: 1000px) {
        grid-column: a /e;
        grid-row: b / c;
    }
`

const ModHeader = styled(Header)`
    grid-column: a / g;
`

const Image = styled.img`
    width: 100%;
    grid-column: d / f;
`

const ProjectContainer = ({themeBackGroundColor, themeMainAccentColor}) => {

    const [curProject, setCurProject] = useState(0);

    return (
        <ProjectWrapper>
            <LeftArrow>
                <Triangle direction='left' color={themeMainAccentColor}/>
            </LeftArrow> 
            <ModHeader>Projects:</ModHeader>
            <RightArrow>
                <Triangle direction='right' color={themeMainAccentColor}/>
            </RightArrow>

                <Image src={StickerImg1}></Image>
        </ProjectWrapper>
    );
}

const mapStateToProps = state => ({
    themeBackGroundColor: selectThemeBackgroundColor(state),
    themeMainAccentColor: selectThemeMainAccentColor(state),
    themeFontColor: selectThemeFontColor(state)
});
const mapDispatchToProps = dispatch => ({});


export default connect(mapStateToProps, mapDispatchToProps)(ProjectContainer);